@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

.profileScreen{
    height:100vh;
    color:#fff;
}
.profileScreen__body{
    display:flex;
    flex-direction:column;
    width:50%;
    margin-left:auto;
    margin-right:auto;
    padding-top:8%;
    max-width:800px;
}
.profileScreen__info{
    display:flex;
}
.profileScreen__body > h2{
    font-size:40px;
    font-family: 'Roboto', sans-serif;
    font-weight:400;
    border-bottom:1px solid #9a0b12;
    margin-top:30px
}
.profileScreen__info > .userLogo{
    font-size: 57px;
    height:100px;
}
.profileScreen__detail{
    color:white;
    margin:20px 0px 0px 20px;
    flex:1;
}
.profileScreen__detail > h2 {
    background-color: gray;
    padding:15px;
    font-size: 15px;
    padding-left:20px;
}
.profileScreen__signout{
    padding:10px 20px;
    font-size:1rem;
    margin-top:5%;
    width:100%;
    color:#fff;
    background-color: #e50914;
    font-weight: 600;
    outline-width: 0;
    border:none;
    border-radius:5px;
    cursor:pointer;
}
.profileScreen__plans h3{
    border-bottom:1px solid #282c2d;
    padding-bottom:10px
}


