@import url(https://fonts.googleapis.com/css2?family=Mate+SC&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
*{
  margin:0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

 
.nav{
    position:fixed;
    top:0;
    padding:30px;
    width:100%;
    height:80px;
    z-index:1;
    /*transition*/
    transition-timing-function: ease-in;
    transition: all 0.5s;
}
.nav__black{
    background-color: #111;
}
.nav__contents{
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content:space-between;
            justify-content:space-between;
}
.nav__logo{
    position:fixed;
    top: 8px;
    left:0;
    width:150px; 
    object-fit: contain;
    cursor: pointer;
    padding-left: 20px;
}
.nav__avatar{
    position:fixed;
    cursor: pointer;
    right:20px;
    width:38px;
    height:38px;
}



.banner{
    position: relative;
    height: 480px;
    color:white; 
    object-fit: scale-down;
}
.banner__contents{
    margin-left:30px;
    padding-top:165px;
    height:190px;
}
.fade__background{
    opacity: 0.1;
    background: linear-gradient(360deg, transparent,#606A7A, #111);
}
.banner__title{
    font-size: 3rem;
    font-weight: 500;
    padding-bottom:0.3rem;
    font-family: 'Roboto', sans-serif;
}
.banner__description{
    width:45rem;
    line-height: 1.3;
    padding-top:1rem;
    font-size:0.8rem;
    max-width:360px;
    height:80px;
}

.banner--fadeBottom{
    margin-bottom:0px;
    padding-bottom: 0px;
    height:18.4rem;
    background-image: linear-gradient(180deg, transparent,rgba(37, 37, 37, 0.61), #111);
}
.banner__button{
    cursor:pointer;
    color:#fff;
    border:none;
    outline:none;
    font-weight:600;
    border-radius:0.2vw;
    padding-right:2rem;
    padding-left:2rem;
    margin-right:1rem;
    padding-top: 0.5rem;
    padding-bottom:0.5rem;
    background-color: rgba(51,51,51,0.5);
}
.banner__button:hover{
    color:#000;
    background-color:#e6e6e6;
    transition: all 0.2s;
}
.row1{
    color:white;
    margin-left:20px;
}
.row__postersMain{
    display:-webkit-flex;
    display:flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding:20px;
}
.row__postersMain::-webkit-scrollbar{
    display:none;
}
.row__poster{
    max-height:100px;
    object-fit:contain;
    margin-right:10px;
    width:100%;
    transition:-webkit-transform 450ms;
    transition:transform 450ms;
    transition:transform 450ms, -webkit-transform 450ms;
}
.row__poster:hover{
    -webkit-transform:scale(1.08);
            transform:scale(1.08);
    opacity:1;
}
.row__posterLarge{
    max-height:250px;
}
.row__posterLarge:hover{
    -webkit-transform:scale(1.09);
            transform:scale(1.09);
    opacity:1;
}


.loginScreen{
    position: relative;
    height:100%;
    background: url("https://assets.nflxext.com/ffe/siteui/vlv3/c0a32732-b033-43b3-be2a-8fee037a6146/2fe6e3c0-5613-4625-a0c1-3d605effd10b/IN-en-20210607-popsignuptwoweeks-perspective_alpha_website_small.jpg") center no-repeat;
    background-size:cover;
}
.loginScreen__logo{
    position:fixed;
    top: 8px;
    left:0;
    width:150px; 
    object-fit: contain;
    cursor: pointer;
    padding-left: 20px;
}
button.loginScreen__button{
    position:fixed;
    cursor: pointer;
    top:20px;
    right:20px;
    color:#fff;
    background:#e50914;
    font-size:1rem; 
    padding:7px 18px;
    border:#e50914;
    font-weight: 400;
    border-radius: 3px;
}
.loginScreen__body > h1{
    margin-bottom:20px;
    font-size:3.125rem;
    line-height: 1.1;
    font-weight: 700;
    font-family: "Netflix Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
}
.loginScreen__body > h2{
    margin-bottom:30px;
    font-size:2rem;
    font-weight:400;
    font-family: 'Mate SC', serif;
    line-height: 1.1;
    word-spacing:-1.93px;
}
.loginScreen__body > h3{
    font-size:1.3rem;
    font-weight:400;
    font-family:"Netflix Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
    line-height: 1.1;
}
.loginScreen__gradient{
    width:100%;
    height: 110vh;
    z-index:1;
    background:rgba(0,0,0,0.4);
    background-image: linear-gradient(to top,rgba(0,0,0,.8) 0,rgba(0,0,0,0) 50%,rgba(0,0,0,0) 75%,rgba(0,0,0,0.8) 100%);
}
.loginScreen__body{
    position:absolute;
    top:20%; 
    z-index:1;
    color:#fff;
    padding:8px;
    text-align:center;
    margin-left:auto;
    margin-right:auto;
    left:0;
    right:0;
}
.loginScreen__input > form > input{
    padding:10px;
    outline-width: 0;
    border:none;
    height: 55px;
    width: 30%;
    max-width:600px;
}
.loginScreen__input .get__started{
    padding:16px 20px;
    font-size:1 rem;
    color:#fff;
    background-color: #e50914;
    border:none;
    font-weight: 600px;
    cursor:pointer;
}
 


.SignUpScreen {
    max-width:450px;
    padding:65px;
    margin-left:auto;
    margin-right:auto;
    background-color: rgba(0,0,0,.65);
    border-radius: 4px;
}
.SignUpScreen > form {
    display:grid;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.SignUpScreen > form > input {
    outline-width: 0;
    height:42px;
    color:#fff;
    background: #333;
    margin-bottom:14px;
    border-radius:5px;
    border:none;
    padding:5px 15px;

}
.SignUpScreen > form > button {
    padding:16px 20px;
    font-size: 1rem;
    color:#fff; 
    border:none;
    outline-width: 0;
    background-color: #e50914;  
    border-radius:5px; 
    font-weight: 600;
    cursor: pointer; 
}
.SignUpScreen > form > h1{
    font-family: 'Lato', sans-serif;
    text-align:left;
    margin-bottom:25px;
}
.signUp__link:hover{
    cursor:pointer;
    text-decoration:underline;
}

.profileScreen{
    height:100vh;
    color:#fff;
}
.profileScreen__body{
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction:column;
            flex-direction:column;
    width:50%;
    margin-left:auto;
    margin-right:auto;
    padding-top:8%;
    max-width:800px;
}
.profileScreen__info{
    display:-webkit-flex;
    display:flex;
}
.profileScreen__body > h2{
    font-size:40px;
    font-family: 'Roboto', sans-serif;
    font-weight:400;
    border-bottom:1px solid #9a0b12;
    margin-top:30px
}
.profileScreen__info > .userLogo{
    font-size: 57px;
    height:100px;
}
.profileScreen__detail{
    color:white;
    margin:20px 0px 0px 20px;
    -webkit-flex:1 1;
            flex:1 1;
}
.profileScreen__detail > h2 {
    background-color: gray;
    padding:15px;
    font-size: 15px;
    padding-left:20px;
}
.profileScreen__signout{
    padding:10px 20px;
    font-size:1rem;
    margin-top:5%;
    width:100%;
    color:#fff;
    background-color: #e50914;
    font-weight: 600;
    outline-width: 0;
    border:none;
    border-radius:5px;
    cursor:pointer;
}
.profileScreen__plans h3{
    border-bottom:1px solid #282c2d;
    padding-bottom:10px
}



.app{
    background-color: #111;
    
}
