.banner{
    position: relative;
    height: 480px;
    color:white; 
    object-fit: scale-down;
}
.banner__contents{
    margin-left:30px;
    padding-top:165px;
    height:190px;
}
.fade__background{
    opacity: 0.1;
    background: linear-gradient(360deg, transparent,#606A7A, #111);
}
.banner__title{
    font-size: 3rem;
    font-weight: 500;
    padding-bottom:0.3rem;
    font-family: 'Roboto', sans-serif;
}
.banner__description{
    width:45rem;
    line-height: 1.3;
    padding-top:1rem;
    font-size:0.8rem;
    max-width:360px;
    height:80px;
}

.banner--fadeBottom{
    margin-bottom:0px;
    padding-bottom: 0px;
    height:18.4rem;
    background-image: linear-gradient(180deg, transparent,rgba(37, 37, 37, 0.61), #111);
}
.banner__button{
    cursor:pointer;
    color:#fff;
    border:none;
    outline:none;
    font-weight:600;
    border-radius:0.2vw;
    padding-right:2rem;
    padding-left:2rem;
    margin-right:1rem;
    padding-top: 0.5rem;
    padding-bottom:0.5rem;
    background-color: rgba(51,51,51,0.5);
}
.banner__button:hover{
    color:#000;
    background-color:#e6e6e6;
    transition: all 0.2s;
}